import React from "react";
import { useOutletContext } from "react-router-dom";
import Contents from "../Components/Contents";
import Introducing from "./Menu/Introducing";
import Drawing from "./Menu/Drawing";
import Graphic from "./Menu/Graphic";
import About from "./Menu/About";
import Text from "./Menu/Text";
import Contact from "./Menu/Contact";

const View = () => {
  const context = useOutletContext();

  const getScreen = () => {
    const path = context.path === "" ? "introducing" : context.path;
    const screens = {
      introducing: <Introducing></Introducing>,
      drawing: <Drawing path={path}></Drawing>,
      graphic: <Graphic path={path}></Graphic>,
      text: <Text path={path}></Text>,
      about: <About path={path}></About>,
      contact: <Contact path={path}></Contact>,
      // painting: <Drawing viewContents={viewContents}></Drawing>,
      // Graphic: <Drawing viewContents={viewContents}></Drawing>,
    };
    return screens[path];
  };
  return <Contents>{getScreen()}</Contents>;
};

export default View;
